<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step = 2">Next</li>
      <li v-if="step == 2" @click="publish">Write</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>
  <h4>안녕 {{$store.state.name}} {{$store.state.age}}</h4>
  <button @click="$store.commit('이름변경')">이름변경버튼</button>
  <button @click="나이변경(10)">나이변경버튼</button>

  <p>{{$store.state.more}}</p>
  <button @click="$store.dispatch('getData')">또보기</button>

  <Container :적용받은필터 = "적용받은필터" :필터들 = "필터들" :포스팅 = "포스팅" :step = "step" :변환파일="변환파일" @write="작성한글 = $event"/>
  
  <!-- <p>{{now()}} {{카운터}}</p>
  <p>{{now2}} {{카운터}}</p>
  <button @click="카운터++">나우버튼</button> -->
  <p>{{age}}</p>
  <p>{{맵이름}}</p>

  <button @click="more(횟수)">더보기</button>

  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>

  <!-- <div v-if="탭상태 == 0">내용0</div>
  <div v-if="탭상태 == 1">내용1</div>
  <div v-if="탭상태 == 2">내용2</div>
  <button @click="탭상태 = 0">버튼0</button>
  <button @click="탭상태 = 1">버튼1</button>
  <button @click="탭상태 = 2">버튼2</button> -->



</template>

<script>
import Container from './components/Container.vue';
import postingData from './assets/postingData.js';
import axios from 'axios';
import filterData from './assets/filterData.js';
import {mapMutations, mapState} from 'vuex';


export default {
  name: "App",
  data(){
    return {
      포스팅: postingData,
      횟수: 0,
      탭상태: 0,
      step: 0,
      변환파일: "",
      작성한글: "",
      필터들: filterData,
      적용받은필터: "",
      카운터: 0,
    }
  },
  mounted() {
    this.emitter.on('적용필터', (a)=>{
      console.log(a);
      this.적용받은필터 = a;
    });
  },
  components: {
    Container: Container,
  },
  computed: {
    now2(){
      return new Date();
    },
    name(){
      return this.$store.state.name;
    },
   ...mapState(['name', 'age', 'likes']),
   ...mapState({ 맵이름: 'name' }),
  },
  methods:{
    ...mapMutations(['setMore', '나이변경']),
   
   now(){
      return new Date();
    },
   
    more(i){
      axios.get("https://codingapple1.github.io/vue/more"+ i +".json")
      .then((결과)=>{
        this.포스팅.push(결과.data);
        this.횟수++;
      })
    },
    upload(e){
      let 파일 = e.target.files;
      this.변환파일 = URL.createObjectURL(파일[0]);
      this.step = 1;
    },
    publish(){
      var 내게시물 = {
            name: "MR.Nb",
            userImage: "https://placeimg.com/100/100/arch",
            postImage: this.변환파일,
            likes: this.$store.state.like,
            date: "May 15",
            liked: false,
            content: this.작성한글,
            filter: this.적용받은필터,
      };
      this.포스팅.unshift(내게시물);
      this.step = 0;

    },
  }
};
</script>

