<template>
  <div @click="fire" :class="`${필터} filter-item`" :style="`background-image: url(${변환파일})`">
      <slot></slot>
     
  </div>
</template>

<script>
export default {
    name: 'filterbox',
    props: {
        변환파일: String,
        필터: String,
    },
    methods: {
        fire(){
            this.emitter.emit('적용필터', this.필터)
        }
    },
}
</script>

<style>
.filter-item {
    width: 100px;
    height: 100px;
    margin: 10px 10px 10px auto;
    padding: 8px;
    display: inline-block;
    color: white;
    background-size: cover;
    background-position: center;
}
</style>