import axios from 'axios';
import { createStore } from 'vuex'

const store = createStore({
    state(){
        return {
            name: 'kim',
            age: 20,
            likes: 30,
            check: false,
            more: {},
        }
    },
    mutations: {
        이름변경(state){
           state.name = 'park';
        },
        나이변경(state, data){
            state.age += data;
        },
        좋아요증가(state){
            if(state.check == false){
                state.likes++;
                state.check = true;
            } else if (state.check == true){
                state.likes--;
                state.check = false;
            }
        },
        setMore(state, data){
            state.more = data;
        }
    },
    actions: {
        getData(context){
            axios.get("https://codingapple1.github.io/vue/more0.json").then((a)=>{
                context.commit('setMore', a.data);
            })
        }
    }
})

export default store;