<template>
  <div>

    <div v-if="step == 0">  
     <Post :포스팅="포스팅[i]" v-for="(item, i) in 포스팅" :key="i" />
    </div>

    <div v-if="step == 1">
    <!-- 필터선택페이지 -->
        <div :class="적용받은필터" class="upload-image" :style="`background-image: url(${변환파일})`"></div>
        <div class="filters">
           <FilterBox :변환파일 = '변환파일' :필터 = "필터" v-for="필터 in 필터들" v-bind:key="필터" >
              <span>{{필터}}</span>
           </FilterBox>
        </div>
    </div>

    <div v-if="step == 2">
        <!-- 글작성페이지 -->
        <div :class="적용받은필터" class="upload-image" :style="`background-image: url(${변환파일})`"></div>
        <div class="write">
            <textarea class="write-box" @input="$emit('write', $event.target.value)">write!</textarea>
        </div>
    </div>
  
  </div>
</template>

<script>
import Post from "./Post";
import FilterBox from './FilterBox.vue';

export default {
  name: "container",
  components: {
    Post: Post,
    FilterBox: FilterBox,

  },
  props: {
    포스팅: Array,
    step: Number,
    변환파일: String,
    필터들: Array,
    적용받은필터: String,
  },
};
</script>

<style>
.upload-image{
width: 100%;
height: 450px;
background: cornflowerblue;
background-size : cover;
}
.filters{
overflow-x:scroll;
white-space: nowrap;
}
.filter-1 {
width: 100px;
height: 100px;
background-color: cornflowerblue;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background-size: cover;
}
.filters::-webkit-scrollbar {
height: 5px;
}
.filters::-webkit-scrollbar-track {
background: #f1f1f1; 
}
.filters::-webkit-scrollbar-thumb {
background: #888; 
border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
background: #555; 
}
.write-box {
border: none;
width: 90%;
height: 100px;
padding: 15px;
margin: auto;
display: block;
outline: none;
}
</style>